.match-list {
    width: 280px;
    background-color: white;
    border-right: 1px solid #eee;
    min-width: 280px;
}

.panel {
    padding: 20px;
}

.panel-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 8px;
}

.panel-icon {
    color: #666;
    font-size: 1.2em;
}

.panel h2 {
    color: #333;
    font-size: 1.1em;
    font-weight: 600;
    margin: 0;
}

.panel-description {
    color: #666;
    font-size: 0.9em;
    margin: 0 0 20px 0;
    text-align: left;
}

.matches {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.match-item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 12px;
    cursor: pointer;
    border-radius: 8px;
    transition: all 0.2s ease;
}

.match-item:hover {
    background-color: #f5f5f5;
}

.match-item.selected {
    background-color: #e8f0fe;
}

.match-item.selected .match-title {
    color: #1a73e8;
}

.match-item.selected .player-name {
    color: #1a73e8;
}

.check-icon {
    color: #1a73e8;
    font-size: 1.1em;
}

.match-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.match-id {
    color: #aaa;
    font-size: 0.9em;
}

.match-date {
    color: #888;
    font-size: 0.8em;
}

.match-players {
    display: flex;
    gap: 12px;
    font-size: 0.85em;
    color: #5f6368;
}

.player-info {
    display: flex;
    align-items: center;
    gap: 4px;
}

.color-indicator {
    font-size: 0.9em;
    line-height: 1;
}

.player-name {
    color: #5f6368;
}

.match-result {
    color: #aaa;
    font-size: 0.9em;
    text-align: center;
    padding-top: 5px;
    border-top: 1px solid #444;
}

.match-list .start-button {
    margin-top: 16px;
    width: 100%;
    padding: 12px;
    background-color: #1a73e8;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: background-color 0.2s;
}

.match-list .start-button:hover:not(:disabled) {
    background-color: #1557b0;
}

.match-list .start-button:disabled {
    background-color: #dadce0;
    cursor: not-allowed;
}

.match-details {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.match-title {
    font-weight: 500;
    color: #202124;
}

.loading-state,
.error-state {
    padding: 20px;
    text-align: center;
    color: #5f6368;
    font-size: 14px;
}

.error-state {
    color: #d93025;
    background-color: #fce8e6;
    border-radius: 4px;
    margin: 16px;
}

.match-status {
    display: flex;
    align-items: center;
    gap: 6px;
    font-size: 0.85em;
    color: #5f6368;
    margin-top: 4px;
}

.status-indicator {
    font-size: 1.1em;
    line-height: 1;
}

.status-text {
    flex: 1;
}

.match-item.selected .match-status {
    color: #1a73e8;
} 