.footer {
    background-color: var(--bg-secondary);
    color: var(--text-primary);
    padding: 40px 0 20px;
    border-top: 1px solid var(--border-color);
    width: 100%;
}

.footer-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    gap: 40px;
}

.footer-section {
    flex: 1;
}

.footer-section h3 {
    color: var(--text-primary);
    font-size: 16px;
    margin-bottom: 16px;
    font-weight: 500;
}

.footer-section p {
    color: var(--text-secondary);
    font-size: 14px;
    line-height: 1.5;
    margin: 8px 0;
}

.footer-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.footer-section ul li {
    margin-bottom: 8px;
}

.footer-section ul li a {
    color: var(--text-secondary);
    text-decoration: none;
    font-size: 14px;
    transition: color 0.2s ease;
}

.footer-section ul li a:hover {
    color: var(--accent-color);
}

.footer-bottom {
    margin-top: 40px;
    padding-top: 20px;
    border-top: 1px solid var(--border-color);
    text-align: center;
}

.footer-bottom p {
    color: var(--text-secondary);
    font-size: 12px;
}

@media (max-width: 768px) {
    .footer-content {
        flex-direction: column;
        gap: 24px;
    }

    .footer-section {
        text-align: center;
    }
} 