.contact-page {
    min-height: 100vh;
    background-color: var(--bg-primary);
    color: var(--text-primary);
    padding: 40px 20px;
}

.contact-content {
    max-width: 800px;
    margin: 0 auto;
}

.contact-content h1 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: var(--text-primary);
    text-align: center;
}

.contact-section {
    margin-bottom: 40px;
    padding: 30px;
    background-color: var(--bg-secondary);
    border-radius: 8px;
    border: 1px solid var(--border-color);
}

.contact-section h2 {
    font-size: 1.5em;
    margin-bottom: 16px;
    color: var(--accent-color);
}

.contact-section p {
    line-height: 1.6;
    color: var(--text-secondary);
    margin-bottom: 24px;
}

.contact-methods {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 30px;
}

.contact-method {
    padding: 20px;
    background-color: var(--bg-primary);
    border: 1px solid var(--border-color);
    border-radius: 6px;
    text-align: center;
}

.contact-method h3 {
    color: var(--text-primary);
    margin-bottom: 8px;
    font-size: 1.2em;
}

.contact-method p {
    color: var(--accent-color);
    margin-bottom: 8px;
    font-size: 1.1em;
}

.contact-method small {
    color: var(--text-secondary);
    font-size: 0.9em;
}

.contact-form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.form-group {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.form-group label {
    color: var(--text-primary);
    font-size: 0.9em;
}

.form-group input,
.form-group textarea {
    padding: 12px;
    border: 1px solid var(--border-color);
    border-radius: 4px;
    background-color: var(--bg-primary);
    color: var(--text-primary);
    font-size: 1em;
    transition: border-color 0.2s ease;
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--accent-color);
}

.form-group textarea {
    resize: vertical;
    min-height: 120px;
}

.submit-button {
    padding: 12px 24px;
    background-color: var(--accent-color);
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.2s ease;
    margin-top: 10px;
}

.submit-button:hover {
    background-color: var(--accent-hover);
}

@media (max-width: 768px) {
    .contact-page {
        padding: 20px;
    }

    .contact-content h1 {
        font-size: 2em;
    }

    .contact-section {
        padding: 20px;
    }

    .contact-methods {
        grid-template-columns: 1fr;
    }
} 