.about-page {
    min-height: 100vh;
    background-color: var(--bg-primary);
    color: var(--text-primary);
    padding: 40px 20px;
}

.about-content {
    max-width: 800px;
    margin: 0 auto;
}

.about-content h1 {
    font-size: 2.5em;
    margin-bottom: 40px;
    color: var(--text-primary);
    text-align: center;
}

.about-section {
    margin-bottom: 40px;
    padding: 20px;
    background-color: var(--bg-secondary);
    border-radius: 8px;
    border: 1px solid var(--border-color);
}

.about-section h2 {
    font-size: 1.5em;
    margin-bottom: 16px;
    color: var(--accent-color);
}

.about-section p {
    line-height: 1.6;
    color: var(--text-secondary);
    margin-bottom: 16px;
}

.about-section ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.about-section ul li {
    margin-bottom: 8px;
    color: var(--text-secondary);
    padding-left: 20px;
    position: relative;
}

.about-section ul li::before {
    content: "•";
    position: absolute;
    left: 0;
    color: var(--accent-color);
}

.contact-list li {
    display: flex;
    align-items: center;
    gap: 8px;
}

@media (max-width: 768px) {
    .about-page {
        padding: 20px;
    }

    .about-content h1 {
        font-size: 2em;
    }

    .about-section {
        padding: 16px;
    }
} 